import React, { useState} from "react"
import { fetchWeather } from './api/fetchWeather'
import { CssBaseline, TextField, AppBar, Toolbar, Typography, Card, CardContent, Grid } from "@material-ui/core";

const App = () => {
    const[query, setQuery] = useState('')
    const[weather, setWeather] = useState({})

    const search = async(e) => {
        if (e.key === "Enter") {
            const data = await fetchWeather(query)
            
            setWeather(data)
            setQuery('')
        }
    }

    return (
        <>
            <CssBaseline />

            <AppBar position="static">
                <Toolbar >
                    <Typography variant="title" color="inherit">
                        Weather App
                    </Typography>
                </Toolbar>  
            </AppBar>

            <Grid container justify="center" alignItems="center" direction="column" style={{ minHeight: "80vh" }} spacing={5}>

            <Grid item>
                <TextField 
                    autoComplete="off"
                    id="outlined-basic" 
                    label="Search a city..." 
                    variant="standard" 
                    type="text" 
                    className="search" 
                    placeholder=""
                    value={query}
                    onChange={(e) => setQuery(e.target.value)}
                    onKeyPress={search}
                    style={{minWidth: "300px"}}
                />
            </Grid>

            

            {/* Card */}

            {weather.main && (
                
                <Card style={{display:"flex", minWidth: "300px", alignItems: "center", justifyContent:"center", textAlign:"center"}}>
                    <CardContent>
                        <Typography variant="h3">
                            {weather.name}
                        </Typography>

                        <Typography variant="subtitle1">
                            {Math.round(weather.main.temp - 273)}&deg;C
                        </Typography>
                        <img src={`https://openweathermap.org/img/wn/${weather.weather[0].icon}@2x.png`} alt={weather.weather[0].description}></img>
                        
                        <Typography variant="subtitle1">{weather.weather[0].description}</Typography>
                    </CardContent>
                </Card>
            )} 

            </Grid>
        </>
    )
}

export default App